import uniCollapse from '../../../components/uni-collapse/uni-collapse.vue'
import uniCollapseItem from '../../../components/uni-collapse-item/uni-collapse-item.vue'
const app = getApp()
var QQMapWX = require('../../../libs/qqmap-wx-jssdk.min.js')
var qqmapsdk = new QQMapWX({
  key: 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'
})
export default {
	
    data() {
            return {
				detail: '',
				PageCur: 'Home',
				CustomBar: this.CustomBar,
				modalName: null,
				modalNameL: null,
                map: {
                	latitude: 39.909,
                	longitude: 116.39742,
                	showLocation: true,
                	covers: [{
                		width: 20,
                		height: 30,
                	    latitude: 39.909,
                	    longitude: 116.39742,
                	    iconPath: '../../../static/input/椭圆10.png',
                		id: 'map',
                	}
                	],
                	 iconPath: '../../../static/input/椭圆10.png',
                	 controls: [{
                		id: 'map',
                		iconPath: '../../../static/input/椭圆10.png',
                		position: { left: 1200, top: 1200, width: 40, height: 40},
                		clickable: false
                	}], 
                },
				list: [],
				oftenList: [],
				address: {
					title: '',
					address: ''
				},
				checked: 0,
				scrollTop: 0,
				mapStatus: 1 // 控制选择地址时 地图不加载附近列表
            }
        },
		created() {
			uni.setNavigationBarTitle({
				title: '搜索地址'
			})
		},
		onLoad() {
			let map = this.map
			// this.getWidthHeight(e => {
			// 	console.log(e);
			//   map.controls[0].position.top = e.height - 45
			//   map.controls[0].position.left = e.width/2 - 10
			//   this.setData({
			// 	map: map
			//   })
			// })
			
		},
    methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		hideModal(e) {
			this.modalName = null
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModalL(e) {
			this.modalNameL = null
		},
		showModalL(e) {
			this.modalNameL = e.currentTarget.dataset.target
		},
		getAddress (){
		    let that = this
		    uni.getLocation({
		      type: 'gcj02',
		      success: function(res) {
		        console.log(res)
		        let map = that.data.map
		        map.longitude = res.longitude
		        map.latitude = res.latitude
		        that.getWidthHeight(e => {
		          map.controls[0].position.top = e.height / 2 - 35
		          map.controls[0].position.left = e.width / 2 - 20
		          that.setData({
		            map: map,
		            position: {
		              longitude: res.longitude,
		              latitude: res.latitude
		            }
		          })
		          that.getAddressList(1)
		        })
		      },
		    })
		  },
		  
		getWidthHeight (fn){
			var query = uni.createSelectorQuery()
			query.select('#map').boundingClientRect()
			query.exec(res => {
			  fn(res[0])
			})
		  },
		getAddressList(s = 0) {
			let that = this
			let position = that.position
			console.log(position)
			qqmapsdk.reverseGeocoder({
				location: {
					latitude: position.latitude,
					longitude: position.longitude
				},
				get_poi: 1,
				poi_options: "page_size=20;page_index=1",
				success: function(e) {
					if (s) {
						e.result.pois[0].select = 1
						that.setData({
							list: e.result.pois,
							address: e.result.pois[0],
							checked: 0
						})
					} else {
						that.setData({
							list: e.result.pois
						})
					}
					
					setTimeout(() => {
						console.log('************');
						that.scrollTop = 1
					}, 1000)
				},
				fail: err => {
					console.log(err)
				}
			})
		},
		mapChange(e) {
			let that = this
			console.log(this.mapStatus);
			clearTimeout(this.timer)
			this.timer = setTimeout(() => {
				if (e.type == 'end') {
					that.mapCtx = uni.createMapContext('map')
					that.mapCtx.getCenterLocation({
						success: res => {
							console.log(res)
							that.setData({
								position: {
									latitude: res.latitude,
									longitude: res.longitude
								}
							})
							if (that.mapStatus) { // 防止地图点击时 进行多次加载
								that.getAddressList(1)
							} else {
								that.mapStatus = 1
							}
						}
					})
				}
			}, 200)
			
		},
		bindAddress(index) {
			let list = this.list
			let map = this.map
			map.latitude = list[index].location.lat
			map.longitude = list[index].location.lng
			this.setData({
				map: map,
				checked: index,
				address: list[index],
				mapStatus: 0
			})
		},
		setData(obj){
			Object.assign(this, obj)
		},
		addressSearch() {
			uni.navigateTo({
				url: '/pages/address-search/address-search'
			})
		},
		submit() {
			let that = this
			let detail = that.detail || ''
			let address = that.address
			let a = {
				address: address.title + detail,
				lat: address.location.lat,
				lng: address.location.lng
			}
			console.log(address, a)
		}
    },
	components: {
		uniCollapse,uniCollapseItem
	}
}
